const wrappDiv = document.createElement('div')
wrappDiv.style.cssText = `
  display: flex;
  justify-content: space-evenly;
  align-item: center;
  `
document.querySelector('body').append(wrappDiv)
function sortByFeedbacks (array) {
  const parentDiv = document.createElement('div')
  const copyArray = JSON.parse(JSON.stringify(array))
  copyArray.forEach(obj => {
    obj.ratingRevievs = +obj.ratingRevievs.replace(/\D/g, '')
  })
  copyArray.sort((a, b) => a.ratingRevievs - b.ratingRevievs)
  copyArray.forEach(item => {
    if (typeof item.price === 'object') {
      item.price = +item.price.newUan.replace(/\D/g, '')
    } else {
      item.price = +item.price.replace(/\D/g, '')
    }
    const div = document.createElement('div')
    div.innerHTML = `
    <h2>Sort by feedback</h2>
    <h3>${item.name}:</h3>
    <p style='font-size: 20px'>Feedback: ${item.ratingRevievs}</p>
    <p style='font-size: 20px'>Price: ${item.price} â‚´</p>
    `
    parentDiv.append(div)
  })
  wrappDiv.append(parentDiv)
}
document.querySelector('.feedback').addEventListener('click', () => {
  sortByFeedbacks(data)
}, { once: true })
function sortByPrice (array) {
  const parentDiv = document.createElement('div')
  const copy = JSON.parse(JSON.stringify(array))
  copy.forEach(item => {
    if (typeof item.price === 'object') {
      item.price = +item.price.newUan.replace(/\D/g, '')
    } else {
      item.price = +item.price.replace(/\D/g, '')
    }
  })
  copy.sort((a, b) => a.price - b.price)
  copy.forEach(item => {
    const div = document.createElement('div')
    div.innerHTML = `
    <h2>Sort by price</h2>
    <h3>${item.name}:</h3>
    <p style='font-size: 20px'>Price: ${item.price} â‚´</p>
    <p style='font-size: 20px'>Feedback: ${item.ratingRevievs}</p>
    `
    parentDiv.append(div)
  })
  wrappDiv.append(parentDiv)
}
document.querySelector('.price').addEventListener('click', () => {
  sortByPrice(data)
}, { once: true })
